import React, { useState } from 'react';
import {
  Link
} from "react-router-dom";

import "../css/Navbar.css"

export default function Navbar(props) {

  const {connectToWeb3,account} = props;

  const [navbarActive, setnavbarActive ] = useState(false);

  const changeBackground = () => {
    if(window.scrollY >= 80){
      setnavbarActive(true)
    }else{
      setnavbarActive(false)
    }
  }
  window.addEventListener('scroll',changeBackground);

  return (
    <nav className={` navbar  navbar-expand-lg navbar-dark fixed-top glassNav ${navbarActive?  ' ' : ' '}`}>
      <div className="container-fluid px-md-5">
        <Link className="navbar-brand" to="/">
          <img className="img-fluid" src="img/logo2.png" alt="" style={{height:60}} />
        </Link>


        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>


        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0 ">
         
            <li className="nav-item">
            <a className="nav-link " href="#">
              <h4 className="nav-link "  >OpenSea</h4>
              </a>
            </li>
            <li className="nav-item">
            <a className="nav-link " href="#">
              <h4 className="nav-link "  >Discord</h4>
              </a>
            </li>

            <li className="nav-item">
              <Link to="/mint-nft" className="nav-link">
              <h4 className="nav-link ">Your Nft</h4>
              </Link>
            </li>
            
          </ul>
        <button onClick={connectToWeb3} className="btn btn-dark btn-lg">{account?`${account.substring(0,3)}...${account.substring(account.length - 4,account.length)} `:'Connect'}</button>
        </div>

      </div>
    </nav>
  )
}
